// В этом файле должны быть стили для БЭМ-блока other-menu, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.other-menu {
  min-height: 225px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.other-menu__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
  align-self: center;
  width: 250px;
}

.other-menu__list-item {
  min-height: 40px;
  position: relative;
  text-transform: uppercase;
  color: #7a0c0e;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.other-menu__list-item > a {
  margin-left: 5px;
}


