// В этом файле должны быть стили для БЭМ-блока map, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.map {
  max-width: 1000px;
  width: 100%;
  padding-left: 35px;
  margin-top: 50px;
}

.map__title {
  line-height: 2;
  font-weight: 600;
  width: 100%;
  font-size: 12px;
  border-bottom: 1px dotted #e0caca;
}

.map__main {
  margin-top: 15px;
}

.map__block {
  font-size: 12px;
  padding-bottom: 10px;
  line-height: 20px;
}

.map__block-title-link,
.map__block-category-title-link,
.map__block-category-list-title-link {
  font-weight: 600;
}

.map__block-category-title-link {
  margin-left: 10px;
}

.map__block-category-list-title-link {
  margin-left: 20px;
}

.map__list-item-link {
  margin-left: 30px;
}

@media (max-width: 860px) {

  .map {
    padding: 0;
  }
}
