// В этом файле должны быть стили для БЭМ-блока login, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.login {
  max-width: 1000px;
  width: 100%;
  padding-left: 50px;
  margin-top: 50px;
}

.login__title {
  text-transform: uppercase;
  line-height: 3;
  font-family: Philosopher, sans-serif;
  width: 100%;
}

.login__form-item {
  display: flex;
  justify-content: flex-start;
}

.login__form-label {
  width: 75px;
  padding: 4px 0 2px;
  font-size: 12px;
}

.login__form-input {
  border: 1px solid #cccccc;
  background: #fff;
  padding: 3px 10px;
  width: 180px;
  margin-bottom: 10px;
  font-size: 14px;
  color: #333;
}

.login__form-checkbox {
  margin-left: 75px;
  margin-right: 5px;
}

.login__form-checkbox-label {
  font-size: 12px;
  padding: 4px 0 2px;
}

.login__form__footer {
  margin-top: 15px;
  display: flex;
  align-items: center;
}

.login__form .login__form-button {
  background: url(../img/login__form-button.jpg) no-repeat;
  width: 78px;
  height: 22px;
  cursor: pointer;
  border: none;
  margin-right: 20px;
  transition: none;
}

.login__form-button:hover {
  background-position: 0 -22px;
}

.login__form-link {
  font-size: 12px;
}
