// В этом файле должны быть стили для БЭМ-блока catalog, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.catalog {
  max-width: 1000px;
  width: 100%;
  padding-left: 35px;
  margin-top: 20px;
}

.catalog__wrap {
  display: flex;
  flex-flow: row wrap;
}

.catalog__title {
  text-transform: uppercase;
  line-height: 3;
  font-family: Philosopher, sans-serif;
  width: 100%;
  border-bottom: 1px dotted #e0caca;
}

.catalog__main {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
}

.catalog__main-title {
  text-transform: uppercase;
  line-height: 3;
  font-family: Philosopher, sans-serif;
  width: 100%;
}

.catalog__main-button {
  max-width: 500px;
  width: 100%;
  margin: 0 auto;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  background: #a1341a;
  border: none;
  min-height: 30px;
  border-radius: 5px;
  background-image: linear-gradient(to top, #912508 0%, #9e3016 25%, #77200b 50%, #b0361c 90%);
  transition: all 0.3s ease;
  outline: none;
  cursor: pointer;
}

.catalog__main-button:hover {
  background-image: linear-gradient(to top, #b0361c 0%, #77200b 25%, #9e3016 50%, #912508 90%);
  opacity: 0.9;
}

.catalog__article-text {
  font-size: 14px;
  text-align: justify;
  margin-top: 25px;
  line-height: 1.3;
}

.catalog__article-text p {
  margin-top: 15px;
  text-indent: 20px;
}

.catalog__article-text p:nth-child(2) {
  color: #0000ff;
}

@media (max-width: 860px) {

  .catalog {
    padding: 0;
  }
}
