// В этом файле должны быть стили для БЭМ-блока forgot-password, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.forgot-password {
  max-width: 1000px;
  width: 100%;
  padding-left: 50px;
  margin-top: 50px;
}

.forgot-password__title {
  text-transform: uppercase;
  line-height: 3;
  font-family: Philosopher, sans-serif;
  width: 100%;
}

.forgot-password__text {
  font-size: 12px;
  margin-bottom: 20px;
  line-height: 15px;
}

.forgot-password__form-item {
  display: flex;
  justify-content: flex-start;
}

.forgot-password__form-label {
  width: 75px;
  padding: 4px 0 2px;
  font-size: 12px;
}

.forgot-password__form-input {
  border: 1px solid #cccccc;
  background: #fff;
  padding: 3px 10px;
  width: 180px;
  margin-bottom: 10px;
  font-size: 14px;
  color: #333;
}

.forgot-password__form__footer {
  margin-top: 15px;
  display: flex;
  align-items: center;
}

.forgot-password__form .forgot-password__form-button {
  background: url(../img/forgot-password-button.jpg) no-repeat;
  width: 112px;
  height: 22px;
  cursor: pointer;
  border: none;
  margin-right: 20px;
  transition: none;
}

.forgot-password__form-button:hover {
  background-position: 0 -22px;
}
