// В этом файле должны быть стили для БЭМ-блока catalog-item, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.catalog-item {
  min-height: 350px;
  margin-bottom: 25px;
  border-right: 1px dotted #e0caca;
  display: flex;
  width: 25%;
  justify-content: center;
  min-width: 230px;
}

.catalog-item:last-child {
  border-right: none;
}

.catalog-item:nth-child(4n) {
  border-right: none;
}

.catalog-item__wrap {
  height: 100%;
  position: relative;
  min-width: 230px;
  max-width: 260px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
}

.catalog-item__stock {
  position: absolute;
  right: 0;
  background-image: url(../img/catalog-item__stock-img.png);
  background-size: cover;
  background-position: center;
  width: 100px;
  height: 30px;
  text-align: center;
  line-height: 1;
}

.catalog-item__stock span {
  font-size: 10px;
  color: #fff;
  text-align: center;
  font-weight: 700;
}

.catalog-item__img {
  max-width: 100%;
  height: 175px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-size: cover;
  border: 1px solid #e6e6e6;
  padding: 10px;
  margin-bottom: 15px;
}

.catalog-item__img-link > img {
  width: 100%;
  height: auto;
}

.catalog-item__title {
  height: 70px;
  line-height: 1.3;
}

.catalog-item__vendor-code,
.catalog-item__old-price-text {
  color: #999;
}

.catalog-item__vendor-code b {
  color: #000;
}

.catalog-item__old-price {
  margin-top: 10px;
}

.catalog-item__old-price-number {
  font-size: 14px;
  font-weight: 900;
  color: #7a0c0e;
  line-height: 1.25;
}

.catalog-item__old-price-number--cross-out {
  text-decoration: line-through;
}

.catalog-item__button {
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  width: 100%;
  background: #a1341a;
  border: none;
  min-height: 30px;
  border-radius: 5px;
  background-image: linear-gradient(to top, #912508 0%, #9e3016 25%, #77200b 50%, #b0361c 90%);
  transition: all 1s ease-in-out;
  outline: none;
  cursor: pointer;
}

.catalog-item__button:hover,
.catalog-item__button:focus,
.catalog-item__button:active {
  background-image: linear-gradient(to top, #b0361c 0%, #77200b 25%, #9e3016 50%, #912508 90%);
  opacity: 0.9;
}

.catalog-item__form {
  margin-top: 10px;
}

@media (max-width: 1280px) and (min-width: 1051px) {

  .catalog-item {
    width: 33%;
  }

  .catalog-item:nth-child(4n) {
    border-right: 1px dotted #ccc;
  }

  .catalog-item:nth-child(3n) {
    border-right: none;
  }
}

@media (max-width: 1280px) {

  .catalog__wrap {
    justify-content: space-around;
  }
}

@media (max-width: 860px) {

  .catalog-item:nth-child(4n) {
    border-right: 1px dotted #ccc;
  }

  .catalog-item:nth-child(3n) {
    border-right: none;
  }

  .search-wrap {
    padding: 0;
  }
}

@media (max-width: 727px) {

  .catalog-item {
    width: 50%;
  }

  .catalog-item:nth-child(3n) {
    border-right: 1px dotted #ccc;
  }

  .catalog-item:nth-child(2n) {
    border-right: none;
  }
}

@media (max-width: 500px) {

  .catalog-item {
    width: 100%;
    border-right: none;
    border-top: 1px dotted #ccc;
  }

  .catalog-item:nth-child(3n) {
    border-right: none;
  }

  .catalog-item:first-child {
    border-top: none;
  }
}

@media (max-width: 1050px) and (min-width: 860px) {

  .catalog-item {
    width: 50%;
  }

  .catalog-item:nth-child(3n) {
    border-right: 1px dotted #ccc;
  }

  .catalog-item:nth-child(2n) {
    border-right: none;
  }
}
