// В этом файле должны быть стили для БЭМ-блока breadcrumbs, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.breadcrumbs {
  max-width: 1000px;
  width: 100%;
  height: 25px;
  border-bottom: 1px dotted #e0caca;
}

.breadcrumbs-list {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  list-style: none;
  overflow: hidden;
  font-size: 12px;
}

.breadcrumbs-list-item a {
  padding-right: 20px;
}

.breadcrumbs-list-item {
  position: relative;
}

.breadcrumbs-list-item:not(:last-of-type):after {
  content: '';
  position: absolute;
  background: url('/img/breadcrumbs-list-item-after.png') no-repeat;
  width: 10px;
  height: 10px;
  display: block;
  top: 3px;
  right: 0;
}
