// В этом файле должны быть стили для БЭМ-блока registration, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.registration {
  max-width: 1000px;
  width: 100%;
  padding-left: 50px;
  margin-top: 50px;
}

.registration__title {
  text-transform: uppercase;
  line-height: 3;
  font-family: Philosopher, sans-serif;
  width: 100%;
  border-bottom: 1px dotted #e0caca;
}

.registration__form {
  margin-top: 15px;
}

.registration__form-item {
  display: flex;
  flex-flow: row nowrap;
}

.registration__form-item--captcha {
  display: flex;
  align-items: center;
}

.registration__form-label {
  display: block;
  font-size: 12px;
  padding-bottom: 10px;
  margin-right: 10px;
}

.registration__form-input {
  border: 1px solid #ccc;
  padding: 3px 10px;
  width: 350px;
  background: #fff;
  margin-bottom: 10px;
  font-size: 14px;
  color: #333;
}

.registration__form-input--captcha {
  height: 40px;
  width: 154px;
  margin-bottom: 0;
  margin-right: 15px;
}

.registration__form-img-captcha {
  margin-top: 21px;
}

.registration__form-about {
  margin-left: 30px;
  margin-top: 10px;
  font-size: 12px;
  min-height: 17px;
  padding-left: 22px;
  background: url(../img/registration__form-about.png) no-repeat left center;
  display: flex;
  align-items: center;
}

.registration__form__footer {
  margin-top: 15px;
  display: flex;
  align-items: center;
}

.registration__form .registration__form-button {
  background: url(../img/registration-button.jpg) no-repeat;
  width: 123px;
  height: 22px;
  cursor: pointer;
  border: none;
  margin-right: 20px;
  transition: none;
}

.registration__form-button:hover {
  background-position: 0 -22px;
}

@media (max-width: 1050px) {

  .registration__form-item {
    flex-flow: column wrap;
    margin: 20px 0;
  }

  .registration__form-about {
    margin: 0;
  }

  .registration__form-about--captcha {
    margin: 10px 0;
  }
}

@media (max-width: 860px) {

  .registration {
    padding: 0;
  }
}

@media (max-width: 480px) {

  .registration__form-item--captcha {
    flex-flow: column nowrap;
  }

  .registration__form-input--captcha {
    margin: 0;
  }

  .registration__form-input {
    width: 200px;
  }
}
