// В этом файле должны быть стили для БЭМ-блока pagination, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.catalog__pagination {
  max-width: 1000px;
  width: 100%;
  height: auto;
  padding: 15px 0 15px 15px;
}

.catalog__pagination-list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.catalog__pagination-list-item {
  margin: 10px 10px 10px 0;
  min-width: 25px;
  padding: 10px;
  height: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid #e6e6e6;
  text-decoration: none;
  background-image: linear-gradient(to top, #fff 0%, #e6e6e6 50%, #fff 100%);
  transition: all 1s ease-in-out;
  font-size: 12px;
  border-radius: 5px;
}

.catalog__pagination-list-item--active,
.catalog__pagination-list-item:hover {
  border: 1px solid #8c2b15;
  background-image: linear-gradient(to top, #912508 0%, #9e3016 25%, #77200b 50%, #b0361c 90%);
  transition: all 1s ease-in-out;
  color: #fff !important;
  cursor: pointer;
}

.catalog__pagination-list-item--active > a,
.catalog__pagination-list-item:hover > a {
  text-decoration: none;
  color: #fff !important;
}

@media (max-width: 480px) {

  .catalog__pagination {
    padding: 0;
  }
}
