// В этом файле должны быть стили для БЭМ-блока article, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
.article {
  width: 100%;
  padding-left: 35px;
  font-size: 14px;

  & h2 {
    font-size: 20px;
    margin: 20px 0;
    text-transform: uppercase;
  }

  & h3 {
    font-size: 18px;
    text-align: center;
    text-transform: uppercase;
    color: #a1341a;
    margin: 20px 0;
  }

  & h4 {
    font-size: 16px;
    color: #7a0c0e;
    margin: 20px 0;
  }

  & p {
    padding: 10px 0;
  }

  & img {
    width: 100%;
    height: inherit;
  }

  & a {
    word-wrap: break-word;
  }

  & table td {
    padding: 2px 5px;
    border: 1px #E39595 solid;
  }

  & table td p,
  & table td span {
    padding: 10px;
  }

  & table h4 {
    text-transform: uppercase;
    font-size: 16px;
    text-align: center;
    font-weight: 800;
  }
}

.article__img {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  width: 100%;
  height: 100%;
  margin: 20px 0;
}

.article__img--inner {
  justify-content: space-around;
}

.article__img--inner img {
  max-height: 200px;
  max-width: 200px;
  padding-top: 10px;
  padding-left: 10px;
}

.article__text--red {
  color: #ff0000;
}

.article__text--inner {
  padding: 10px 0 10px 30px !important;
}

.article__text--padding {
  padding: 0 !important;
}

.article__block {
  display: flex;
}

.article__block-img {
  display: flex;
  align-items: center;
}

.article__block-img img {
  width: 150px;
  height: auto;
}

.article__img--payment {
  justify-content: flex-start;
}

.article__img--payment img {
  width: 250px;
  height: auto;
}

.article__text--arrow {
  position: relative;
}

.article__text--arrow:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 14px;
  width: 3px;
  height: 5px;
  background: url(../img/article-table-arrow.png) no-repeat;
  background-size: 100%;
}

.article__text {
  padding-left: 100px;
  display: flex;
  flex-flow: row wrap;
}

.article__text h3 {
  text-align: left;
}

.article__block-wrap {
  display: flex;
}

.article__block-wrap h3 {
  padding-left: 20px;
}

.article__block-text h3 {
  text-align: left;
}

.article__block .article__block-img .article__block-img--size {
  width: 250px;
}

.article__block--size {
  max-width: 800px;
}

@media (max-width: 860px) {

  .article {
    padding: 0;
  }
}

@media (max-width: 1000px) {

  .article__block {
    flex-direction: column;
  }
}

@media (max-width: 480px) {

  .article__text {
    padding-left: 20px;
  }
}

