// В этом файле должны быть стили для БЭМ-блока aside-submenu, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.aside-submenu {
  max-width: 295.42px;
  border: 1px solid #ccc;
  margin: 20px 0 0;
}

.aside-submenu__item {
  border-top: 1px solid #ccc;
  line-height: 35px;
  padding-left: 27.5px;
  font-size: 12px;
}

.aside-submenu__item--active {
  background: #e6e6e6;
}

.aside-submenu__item-sublist {
  background: #fff;
  margin-left: -27.5px;
  padding-left: 45px;
  border-top: 1px dotted #e0caca;
}

.aside-submenu__item-sublist-item {
  padding-left: 20px;
  position: relative;
  background: url(/img/aside-submenu__item-sublist-item-img.png) no-repeat 10px 10px;
  line-height: 25px;
}

.aside-submenu__item-sublist-item:not(:last-of-type) {
  border-bottom: 1px solid #ccc;
}
