// В этом файле должны быть стили для БЭМ-блока category, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
@import url('https://fonts.googleapis.com/css?family=Philosopher');

.category {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
  width: 300px;
}

.category__block {
  width: 100%;
}

.category__title {
  text-transform: uppercase;
  line-height: 3;
  padding-left: 27.5px;
  font-family: Philosopher, sans-serif;
}

.category__list-item {
  border-bottom: 1px dotted #e0caca;
  line-height: 24px;
  font-size: 12px;
  color: #7a0c0e;
  padding-left: 27.5px;
}

.category__list {
  margin-bottom: 20px;
}
