// В этом файле должны быть стили для БЭМ-блока navigation-mobile, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.navigation-mobile {
  display: none;
}

@media (max-width: 480px) {

  .navigation-mobile {
    display: flex;
    justify-content: center;
    height: 100%;
    width: 100%;
    background: #a1341a;
    margin-bottom: 20px;
  }

  .navigation-mobile a {
    display: block;
  }

  .navigation-mobile-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 50px;
  }

  .navigation-mobile__hamburger img {
    width: 35px;
    height: 35px;
  }

  .navigation-mobile__logo-pic img {
    width: 70px;
    height: 100px;
  }

  .navigation-mobile__list-container {
    display: none;
  }

  .navigation-mobile__list {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #a1341a;
    padding: 100px 50px;
    overflow: auto;
  }

  .navigation-mobile__list-item {
    padding: 10px 0;
  }

  .navigation-mobile__list-item > a {
    font-size: 18px;
  }

  .dropdown__navigation-mobile-item {
    padding: 10px;
  }

  .dropdown__navigation-mobile-item:first-child {
    padding-top: 20px;
  }

  .drop-navigation-mobile.navigation-mobile__list-container {
    display: block;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 10;
    overflow: hidden;
  }

  .navigation-mobile__close {
    position: absolute;
    top: 40px;
    right: 50px;
    z-index: 15;
  }

  .navigation-mobile__close img {
    width: 25px;
    height: 25px;
  }

  .navigation-mobile__list-item-dropdown__navigation-mobile {
    display: none;
  }

  .drop-submenu.navigation-mobile__list-item-dropdown__navigation-mobile {
    display: block;
  }
}
