// В этом файле должны быть стили для БЭМ-блока filter, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.catalog-filter {
  max-width: 1000px;
  width: 100%;
  height: auto;
  border-bottom: 1px dotted #e0caca;
  padding: 5px 0 5px 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row wrap;
}

.catalog-filter > div {
  margin: 10px 0;
}

.catalog-filter__show-count {
  max-width: 175px;
  width: 100%;
  min-width: 100px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.catalog-filter__show-sort {
  max-width: 500px;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
}

.catalog-filter__show-sort-label,
.catalog-filter__show-sort-select-wrap {
  width: 100%;
}

.catalog-filter__show-count-label,
.catalog-filter__show-sort-label {
  font-size: 12px;
  display: flex;
  align-items: center;
}

.catalog-filter__show-count-select {
  width: 50px;
  text-align: center;
  appearance: menulist !important;
  padding: 0 5px;
  border: 1px solid #e6e6e6;
  margin-left: 10px;
}

.catalog-filter__show-sort-select {
  width: 185px;
  text-align: center;
  appearance: menulist !important;
  padding: 0 20px;
  border: 1px solid #e6e6e6;
  margin-left: 10px;
}

.catalog-filter__buttons > a {
  display: inline-block;
  width: 50px;
  height: 50px;
  margin-right: 1px;
  background-color: #ccc;
  cursor: pointer;
  margin-left: 5px;
}

.catalog-filter__buttons-table--active,
.catalog-filter__buttons-list--active {
  background-color: #7a0c0e !important;
}

.catalog-filter__buttons-table span {
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url(/img/catalog-filter-table-template.png);
  margin: 17px;
}

.catalog-filter__buttons-list span {
  background: url(/img/catalog-filter-list-template.png);
  display: inline-block;
  width: 16px;
  height: 16px;
  margin: 17px;
}

.catalog-filter__show-sort-select-wrap {
  display: flex;
}

@media (max-width: 1115px) {

  .catalog-filter__show-sort-label,
  .catalog-filter__show-count-label {
    justify-content: center;
  }

  .catalog-filter__show-sort-select-wrap,
  .catalog-filter__show-count-select {
    display: flex;
    justify-content: center;
    margin: 10px 0 0;
  }

  .catalog-filter__show-sort-select {
    margin-left: 10px;
  }

  .catalog-filter__show-count,
  .catalog-filter__show-sort {
    flex-direction: column;
  }
}

@media (max-width: 640px) {

  .catalog-filter {
    flex-direction: column;
  }

  .catalog-filter__show-sort {
    margin: 30px 0;
  }
}

@media (max-width: 480px) {

  .catalog-filter {
    padding: 0;
  }

  .catalog-filter__show-sort-select-wrap {
    flex-direction: column;
    align-items: center;
  }

  .catalog-filter__show-sort-select {
    margin: 5px 0 0;
  }
}
