// В этом файле должны быть стили для БЭМ-блока cart-item, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.cart-item {
  max-width: 1000px;
  width: 100%;
  padding-left: 35px;
  margin-top: 20px;
}

.cart-item__wrap {
  max-width: 100%;
  width: 100%;
}

.cart-item__title {
  text-transform: uppercase;
  line-height: 3;
  font-family: Philosopher, sans-serif;
}

.cart-item__block {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: flex-start;
  height: auto;
  border-bottom: 1px dotted #e0caca;
  padding-bottom: 25px;
}

.cart-item__block-image {
  width: 550px;
  height: auto;
  padding: 5px;
}

.cart-item__block-image-preview {
  width: 100%;
  overflow: hidden;
  position: relative;
}

.cart-item__block-image-preview-img {
  width: 100%;
  overflow: hidden;
  height: auto;
  padding: 5px;
  border: 1px solid #e6e6e6;
}

.cart-item__block-image-preview-stock {
  position: absolute;
  right: 5px;
  top: 5px;
  background-image: url('/img/cart-item__block-image-preview-stock.png');
  background-size: cover;
  background-position: center;
  width: 120px;
  height: 30px;
  text-align: center;
  line-height: 1.65;
  font-size: 12px;
  color: #fff;
  font-weight: 600;
}

.cart-item__block-image-thumbs {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
}

.cart-item__block-image-thumbs-img {
  height: 100px;
  width: auto;
  padding: 2.5px;
  border: 1px solid #e6e6e6;
  margin-bottom: 5px;
}

.cart-item__block-description {
  width: 425px;
  height: auto;
  padding: 10px 30px;
  font-size: 12px;
}

.cart-item__block-description-manufacturer,
.cart-item__block-description-vendor,
.cart-item__block-description-category {
  padding: 5px 0;
}

.cart-item__block-description-manufacturer-item,
.cart-item__block-description-vendor-item,
.cart-item__block-description-item,
.cart-item__block-description-old-price-item,
.cart-item__block-description-main-price-item,
.cart-item__block-description-block-item {
  color: #999;
}

.cart-item__block-description-old-price {
  line-height: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.cart-item__block-description-old-price-number {
  font-size: 15px;
  font-weight: 600;
}

.cart-item__block-description-old-price-number--cross-out {
  text-decoration: line-through;
}

.cart-item__block-description-old-price-economy {
  color: #cb8c05;
}

.cart-item__block-description-main-price {
  line-height: 40px;
  border-bottom: 1px dotted #e0caca;
  border-top: 1px dotted #e0caca;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}

.cart-item__block-description-main-price-number {
  color: #7a0c0e;
  font-size: 15px;
  font-weight: 600;
}

.cart-item__block-description-main-price-button {
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  background: #a1341a;
  border: none;
  min-height: 30px;
  border-radius: 5px;
  background-image: linear-gradient(to top, #912508 0%, #9e3016 25%, #77200b 50%, #b0361c 90%);
  transition: all 1s ease-in-out;
  outline: none;
  cursor: pointer;
  margin-top: 5px;
}

.cart-item__block-description-main-price-button:hover,
.cart-item__block-description-main-price-button:focus,
.cart-item__block-description-main-price-button:active {
  background-image: linear-gradient(to top, #b0361c 0%, #77200b 25%, #9e3016 50%, #912508 90%);
  opacity: 0.9;
}

.cart-item__block-description-block {
  display: flex;
  flex-direction: column;
  justify-content: right;
  width: 100%;
}

.cart-item__block-description-block-item {
  line-height: 30px;
}

.cart-item__block-description-block-exclusive {
  color: #ff0000;
  font-style: italic;
}

.cart-item__block-description-block-list-item {
  margin: 10px 0;
  list-style: none;
  background: url(/img/cart-item__block-description-block-list-item-img.png) no-repeat left 5px;
  padding: 2px 2px 2px 10px;
}

.cart-item__block-description-block-list-item:last-child {
  color: rgb(212, 0, 255);
  font-style: italic;
  text-transform: uppercase;
}

.cart-item__block-description-block-list-item span {
  font-weight: 600;
}

.cart-item__block-description-delivery,
.cart-item__block-description-payment {
  width: 100%;
}

.cart-item__block-description-delivery-title,
.cart-item__block-description-payment-title {
  width: 100%;
  text-align: center;
  background: #e6e6e6;
  line-height: 2;
  text-transform: uppercase;
}

.cart-item__block-description-delivery-main,
.cart-item__block-description-payment-main {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 10px 0;
}

.cart-item__block-description-payment-main-img,
.cart-item__block-description-delivery-main-img {
  width: 25%;
  text-align: center;
}

.cart-item__block-description-payment-main-links,
.cart-item__block-description-delivery-main-links {
  width: 65%;
  display: flex;
  flex-direction: column;
  justify-content: left;
}

.cart-item__block-description-payment-main-links a,
.cart-item__block-description-delivery-main-links a {
  color: #4393ce;
  line-height: 20px;
}

.cart-item__block-description-offer {
  width: 100%;
  background: #f4f4f4;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
}

/* Create four equal columns that floats next to eachother */
.cart-item__block-modal-column-thumb {
  width: 150px;
  height: 150px;
  overflow: hidden;
  padding: 10px;
  float: left;
  background-size: cover;
}

/* The Modal (background) */
.cart-item__block-modal {
  display: none;
  position: fixed;
  z-index: 1;
  padding: 100px 0;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #000000;
}

/* Modal Content */
.cart-item__block-modal-content {
  position: relative;
  background-color: #fefefe;
  margin: auto;
  padding: 0;
  width: 100%;
  max-width: 800px;
}

/* The Close Button */
.close {
  color: #fff;
  position: absolute;
  top: 50px;
  right: 25px;
  font-size: 35px;
  font-weight: 700;
}

.close:hover,
.close:focus {
  color: #999;
  text-decoration: none;
  cursor: pointer;
}

/* Hide the slides by default */
.cart-item__block-modal-content-item {
  display: none;
  text-align: center;
}

.cart-item__block-modal-content-item img {
  width: 50%;
}

/* Next & previous buttons */
.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 16px;
  margin-top: -50px;
  color: #fff;
  font-weight: 700;
  font-size: 20px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

/* Number text (1/3 etc) */
.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

/* Caption text */
.caption-container {
  text-align: center;
  background-color: #000000;
  padding: 2px 16px;
  color: #fff;
}

.cart-item__block-modal-column-thumb-img {
  opacity: 0.6;
  width: 100%;
}

.active,
.cart-item__block-modal-column-thumb-img:hover {
  opacity: 1;
}

.hover-shadow {
  transition: 0.3s;
}

.hover-shadow:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@media (max-width: 860px) {

  .cart-item {
    padding: 0;
  }
}

@media (max-width: 480px) {

  .cart-item__block-description {
    padding: 10px 0;
  }

  .cart-item__block-description-old-price-economy {
    font-size: 10px;
  }
}
