// В этом файле должны быть стили для БЭМ-блока footer, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.footer {
  font-size: 12px;
  background-image: url(../img/footer_bg.png);
  background-repeat: no-repeat;
  background-position: center;
  min-height: 125px;
  max-width: 1073px;
  margin: 0 auto;
}

.footer__wrap {
  display: flex;
  justify-content: space-between;
  padding: 30px 55px 10px;
  width: 100%;
}

.footer__left-copytight__link {
  color: #7a0c0e;
  text-decoration: none;
}

.footer__left-copytight__link:hover {
  color: #000;
  text-decoration: underline;
}

.footer__left-portaluser-img {
  max-height: 70px;
  width: auto;
  margin-top: 15px;
}

.footer__middle {
  margin-left: -300px;
}

.footer__middle-contacts,
.footer__right {
  display: flex;
  flex-direction: column;
}

.footer__middle-contacts-link,
.footer__right-link {
  color: #000;
  text-decoration: none;
}

.footer__right {
  position: relative;
}

.footer__right-bitrix {
  position: absolute;
  bottom: 0;
  right: -13px;
}

.footer__right a {
  padding: 5px 0;
}

@media (max-width: 1289px) {

  .footer__middle {
    margin-left: 0;
  }
}

@media (max-width: 769px) {

  .footer__middle {
    display: none;
  }
}

@media (max-width: 590px) {

  .footer__wrap {
    flex-direction: column;
    text-align: center;
  }

  .footer__left {
    order: 2;
    margin-top: 10px;
  }

  .footer__right {
    flex-direction: row;
    justify-content: space-around;
    order: 1;
  }

  .footer__left-portaluser-img,
  .footer__right-bitrix {
    display: none;
  }
}
