// В этом файле должны быть стили для БЭМ-блока category-page, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.category-page {
  font-size: 14px;
  width: 100%;

  & h2 {
    font-size: 20px;
    margin: 10px 0;
    text-transform: uppercase;
  }

  & h3 {
    font-size: 22px;
  }
}

.category-page-wrap {
  padding-left: 35px;
}

.category-page__text--color {
  color: #ff0000;
  font-size: 16px;
  padding: 10px 0;
}

.category-page__main {
  margin: 10px 0 20px;
  display: flex;
  flex-wrap: wrap;
}

.category-page__main-item {
  flex-basis: 33%;
  border-right: 1px dotted #e0caca;
  border-top: 1px dotted #e0caca;
  padding: 25px;
  min-height: 350px;
  max-width: 390px;
  min-width: 260px;
}

.category-page__main-item:first-child,
.category-page__main-item:nth-child(2),
.category-page__main-item:nth-child(3) {
  border-top: none;
}

.category-page__main-item:nth-child(3n),
.category-page__main-item:last-child {
  border-right: none;
}

.category-page__main-item .item__title {
  padding: 10px 0;
}

.category-page__main-item .item__list-item {
  padding: 10px 0;
}

.category-page__main-item .item__list-item a {
  word-wrap: break-word;
}

.category-page__main-item .item__img-wrap a {
  display: block;
}

@media (max-width: 1267px) {

  .category-page__main {
    justify-content: center;
  }

  .category-page__main-item {
    flex-basis: 50%;
  }

  .category-page__main-item:nth-child(3) {
    border-top: 1px dotted #e0caca;
  }

  .category-page__main-item:nth-child(3n) {
    border-right: 1px dotted #e0caca;
  }

  .category-page__main-item:nth-child(2n) {
    border-right: none;
  }
}

@media (max-width: 860px) {

  .category-page-wrap {
    padding: 0;
  }
}

@media (max-width: 539px) {

  .category-page__main-item {
    border-right: none;
  }

  .category-page__main-item:nth-child(2n) {
    border-top: 1px dotted #e0caca;
  }

  .category-page__main-item:nth-child(3n) {
    border-right: none;
  }

  .category-page__main-item:first-child {
    border-top: none;
  }
}

