// В этом файле должны быть стили для БЭМ-блока article-preview, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.article-preview {
  max-width: 1300px;
  width: 100%;
}

.article-preview a {
  font-weight: 700;
}

.article-preview__wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
  padding: 27.5px;
  border-top: 1px dotted #e0caca;
}

.article-preview__title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 272.5px;
  width: 100%;
  height: 35px;
}

.article-preview__title h3 {
  font-family: Philosopher, sans-serif;
  text-transform: uppercase;
  line-height: 15px;
}

.article-preview__title a {
  font-size: 13px;
  line-height: 15px;
}

.article-preview__block-wrap {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  max-width: 1272.5px;
  width: 100%;
  flex-wrap: wrap;
  align-content: center;
}

.article-preview__block {
  max-width: 275px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 20px;
}

.article-preview__block-date span {
  font-size: 12px;
  line-height: 30px;
}

.article-preview__block-img {
  height: 200px;
  overflow: hidden;
  border-radius: 50%;
  margin-top: 25px;
  align-self: center;
}

.article-preview__block-img a {
  display: block;
}

.article-preview__block-text {
  font-size: 12px;
  text-align: justify;
  height: 75px;
}

