// В этом файле должны быть стили для БЭМ-блока price-list, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.price-list {
  max-width: 1000px;
  width: 100%;
  padding-left: 35px;
  margin-top: 50px;
}

.price-list__title {
  line-height: 2;
  font-weight: 600;
  width: 100%;
  font-size: 12px;
  border-bottom: 1px dotted #e0caca;
}

.price-list__table {
  border: 1px solid #000;
  width: 100%;
  margin-top: 15px;
  font-size: 12px;
}

.price-list__table-header {
  line-height: 20px;
}

.price-list__table-title,
.price-list__table-name {
  font-weight: 600;
}

.price-list__table-item {
  border: 1px dotted #000;
  line-height: 20px;
}

.price-list__table-item--header {
  border: 1px solid #000;
}

.price-list__table-price {
  text-align: center;
  border-left: 1px dotted #000;
}

.price-list__table-link {
  padding-left: 15px;
}

@media (max-width: 860px) {

  .price-list {
    padding: 0;
  }
}

@media (max-width: 480px) {

  .price-list__table {
    font-size: 11px;
  }

  .price-list__table-link {
    padding: 0;
  }
}
