// В этом файле должны быть стили для БЭМ-блока search, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.search {
  width: 100%;
  height: 100%;
  min-height: 350px;
}

.search-wrap {
  padding-left: 35px;
}

.search__title {
  font-size: 20px;
  padding: 20px 0;
  text-transform: uppercase;
}

.search__form {
  width: 100%;
}

.search__form input {
  min-width: 300px;
  padding: 3px;
}

.search__form button {
  margin-left: 10px;
  cursor: pointer;
  padding: 3px;
}

.search__text {
  margin-top: 15px;
}

.search__text-item--color {
  color: #ff1122;
}

.search__text-block {
  margin-top: 20px;
}

.search__text-block h3 {
  padding: 10px 0;
}

.search__text-table tr td {
  padding: 10px 0;
  text-align: center;
  min-width: 150px;
}

.search__text-table tr td:last-child {
  text-align: left;
}

.search-product .search__form {
  padding-bottom: 20px;
  border-bottom: 1px dotted #ccc;
}

.search-product .catalog {
  padding: 0;
}

@media (max-width: 480px) {

  .search__text-block--visible {
    display: none;
  }
}
