// В этом файле должны быть стили для БЭМ-блока fast-search, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.fast-search {
  width: 100%;
  max-width: 295.42px;
  border: 1px solid rgb(210, 210, 210);
  padding: 15px 10px;
}

.fast-search-label {
  display: block;
  width: 100%;
  font-size: 12px;
  padding-bottom: 10px;
  color: #666666;
}

.fast-search-select {
  appearance: menulist !important;
  width: 100%;
  margin-bottom: 15px;
  padding: 4px 10px;
}

.fast-search-price {
  position: relative;
  height: 5px;
  border: 1px solid #d4d4d4;
  margin-bottom: 15px;
}

.fast-search-price-line {
  background: #7a0c0e;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
}

.fast-search-price-handle {
  position: absolute;
  top: -7px;
  z-index: 2;
  width: 8px;
  height: 15px;
  background: url(../img/fast-search-price-handle.png) no-repeat;
  cursor: pointer;
  border-radius: 4px;
}

.fast-search-price-handle--left {
  left: -1px;
}

.fast-search-price-handle--right {
  right: -1px;
}

.fast-search-price-widget {
  display: flex;
  justify-content: space-between;
}

.fast-search-price-diapason {
  font-size: 12px;
  line-height: 22px;
}

.fast-search-price-input {
  border: 1px solid #ccc;
  width: 85px;
  font-size: 13px;
  color: #000;
  padding: 4px 10px;
}

.fast-search-price-btn {
  display: flex;
  justify-content: flex-end;
}

.fast-search-price-btn-input {
  margin-top: 8px;
  background: url(../img/fast-search-price-btn.jpg) no-repeat;
  width: 52px;
  height: 22px;
  cursor: pointer;
  border: none;
}
