*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  font-family: Verdana, sans-serif;
  font-size: 16px;
  // color: $text-color;
  background: $body-bg;
  min-height: 100vh;
  color: $black;
  min-width: 320px;
}

a {
  color: #7a0c0e;
  text-decoration: none;
}

a:hover {
  color: #333;
  text-decoration: underline;
}

.clearfix {
  content: '';
  display: table;
  clear: both;
}

select {
  line-height: 1em;
  cursor: pointer;
  text-indent: 0.01px;
  text-overflow: '';
  appearance: none !important;
}

select::-ms-expand {
  display: none;
}

.main__content {
  width: 100%;
  padding-top: 20px;
}

.visually-hidden {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  width: 0;
  height: 0;
}

main {
  padding: 0 10px;
  margin-bottom: 40px;
}

.hidden {
  display: none;
}

.flex {
  display: flex;
  flex-flow: row nowrap;
}

.container-inner {
  display: flex;
  justify-content: center;
}

.container {
  max-width: 1300px;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
}

.container--flex {
  display: flex;
  flex-flow: row nowrap;
}

.main.container-inner {
  flex-direction: column;
  align-items: center;
}

.index-page-container {
  flex-flow: row wrap;
}

.button {

}

@media (max-width: 769px) {

  .page__content {
    margin-left: 0;
  }
}

@media (max-width: 480px) {

  .container--flex {
    flex-flow: column nowrap;
  }
}
