// В этом файле должны быть стили для БЭМ-блока header, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.header__fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0 10px;
  z-index: 5;
}

.header__fixed-wrap {
  padding: 0 171px 0 30px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  height: 40px;
  position: relative;
  font-size: 14px;
}

.header__fixed-wrap:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  border-top: 40px solid #dedccd;
  border-left: 13px solid transparent;
  border-right: 13px solid transparent;
  z-index: -1;
}

.header__fixed-list {
  display: flex;
  flex-flow: row nowrap;
  height: 100%;
  align-items: center;
}

.header__fixed-list-item {
  padding-left: 15px;
  position: relative;
}

.header__fixed-list-item:before {
  position: absolute;
  font-family: Arial;
  content: '\2022';
  top: 0;
  left: 5px;
}

.header__fixed-list-item:first-child:before {
  content: '';
}

.header__fixed-contacts {
  display: flex;
  height: 100%;
  align-items: center;
}

.header__fixed-contacts-tel {
  position: relative;
}

.header__fixed-contacts-tel:before {
  content: '';
  position: absolute;
  top: -1px;
  left: -15px;
  width: 11px;
  height: 17px;
  background-image: url(../img/phone.png);
  background-repeat: no-repeat;
  background-size: 100%;
}

.header__fixed-contacts-tel:hover {
  text-decoration: none;
  color: #7a0c0e;
}

.header__fixed-contacts-email {
  margin-left: 15px;
}

.header__fixed-buttons {
  display: flex;
  height: 100%;
  align-items: center;
}

.header__fixed-login,
.header__fixed-registration {
  line-height: 26px;
  padding: 0 15px;
  text-align: center;
}

.header__fixed-login:hover,
.header__fixed-registration:hover {
  background: url(../img/link-login.jpg) no-repeat;
  color: #fff;
  background-size: cover;
  line-height: 26px;
  padding: 0 15px;
  text-decoration: none;
}

.header__fixed-dropdown {
  display: flex;
  flex-flow: row nowrap;
}

.header__fixed-basket {
  display: flex;
  position: absolute;
  right: 31px;
}

.basket-inner {
  text-align: center;
  height: 40px;
  width: 156px;
  position: absolute;
  top: -13px;
  background-color: #efbf00;
  transform: skew(-18deg);
  right: -23px;
}

.header__fixed-basket-link {
  position: relative;
}

.header__fixed-basket-link:hover {
  color: #fff;
  text-decoration: none;
}

.header__fixed-basket-link:before {
  content: '';
  position: absolute;
  top: 1;
  left: -15px;
  width: 12px;
  height: 12px;
  background-image: url(../img/basket-icon.png);
  background-repeat: no-repeat;
  background-position: center;
}

.header__fixed-auth {
  display: none;
}

.header__top {
  justify-content: center;
  align-items: center;
  height: 175px;
}

.header__top-inner {
  height: 100%;
  background: url(../img/header-inner-bg.png) no-repeat;
  background-size: 100%;
}

.header__top-container {
  justify-content: space-between;
  color: #7a0c0e;
  margin-top: 55px;
  padding: 0 80px;
}

.header__top-logo:hover {
  text-decoration: none;
  color: inherit;
}

.header__top-contacts,
.header__top-logo {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.header__top-contacts-tel {
  font-size: 24px;
}

.header__top-contacts-info {
  font-size: 13px;
}

.header__top-logo-brand {
  font-size: 45px;
  font-weight: 800;
}

.header__top-logo-info {
  font-size: 30px;
}

.header__slider {
  display: flex;
  justify-content: center;
  padding-top: 30px;
}

.header__slider-container {
  max-width: 1000px;
  overflow: hidden;
}

.header__slider-item {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  width: 100%;
  position: relative;
}

.header__slider-item-img {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 280px;
}

.header__slider-item-text {
  background: rgba(255, 255, 255, 0.7);
  color: #7a0c0e;
  position: absolute;
  top: 0;
  left: 30px;
  padding: 20px 35px;
  height: 100%;
  max-width: 400px;
}

.header__slider-item-text-top {
  font-size: 32px;
  font-family: Philosopher, sans-serif;
  text-transform: uppercase;
}

.header__slider-item-text-about {
  font-size: 16px;
  font-family: Philosopher, sans-serif;
  text-transform: none;
  line-height: 20px;
  margin: 15px 0;
  display: block;
}

.header__slider-item-text-top-about {
  font-size: 16px;
  font-family: Philosopher, sans-serif;
  text-transform: none;
  line-height: 20px;
  display: block;
}

.header__slider-btn {
  background: url(../img/header__slider-btn.jpg) no-repeat;
  width: 87px;
  height: 24px;
  display: block;
  float: left;
  margin: 10px 20px 0 0;
}

.header__slider-item-text-more {
  font-size: 13px;
  color: #333;
  display: block;
  margin: 5px 0;
}

.header__slider-more-link {
  font-size: 13px;
  color: #7a0c0e;
  float: left;
  display: block;
  padding-top: 15px;
  text-decoration: underline;
}

.owl-theme .owl-dots .owl-dot.active,
.owl-theme .owl-dots .owl-dot:hover span {
  background: url(../img/slider-active.png) no-repeat;
}

.owl-theme .owl-dots .owl-dot {
  background: url(../img/slider-non-active.png);
  width: 14px;
  height: 14px;
  margin: 0 5px;
  display: inline-block;
}

.header__slider-container .owl-dots {
  margin-top: 0;
  position: absolute;
  right: 30px;
  bottom: 0;
}

.owl-theme .owl-dots .owl-dot span {
  opacity: 0;
}

.owl-carousel .owl-nav .owl-prev,
.owl-carousel .owl-nav .owl-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 35px;
  height: 60px;
}

.owl-carousel .owl-nav .owl-prev {
  left: 0;
  background: url(../img/sl_prev.png);
}

.owl-carousel .owl-nav .owl-next {
  right: 0;
  background: url(../img/sl_next.png);
}

.header__bottom {
  width: 100%;
  height: 75px;
  justify-content: center;
}

.header__bottom-container {
  border-bottom: 1px solid #dbdbdb;
  border-right: 1px solid #dbdbdb;
  border-left: 1px solid #dbdbdb;
}

.header__bottom-schedule,
.header__bottom-form {
  flex-basis: 50%;
}

.header__bottom-schedule {
  padding: 10px 0 5px 50px;
}

.header__bottom-schedule-everytime {
  color: #c50007;
}

.header__bottom-schedule p {
  position: relative;
}

.header__bottom-schedule p:before {
  content: '';
  position: absolute;
  display: block;
  left: -23px;
  top: 0;
  width: 16px;
  height: 16px;
  background: url(../img/header-clock.png) no-repeat;
  background-size: 100%;
}

.header__bottom-form {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;
}

.header__bottom-search--container {
  position: relative;
}

.header__bottom-search--container:before {
  content: '';
  display: block;
  width: 20px;
  height: 16px;
  position: absolute;
  top: 0;
  left: -27px;
  background: url(../img/header-search.png) no-repeat;
  background-size: 100%;
}

.header__bottom-button {
  margin-left: 10px;
  cursor: pointer;
}

.index-page .header__top {
  display: none;
}

@media (max-width: 650px) {

  .header__bottom {
    height: 130px;
  }

  .header__bottom-container {
    flex-flow: column nowrap;
    align-items: center;
  }

  .header__bottom-schedule {
    padding: 10px 0 5px 20px;
  }
}

@media (min-width: 992px) {

  .header__slider-container {
    height: 280px;
  }
}

@media (max-width: 991px) {

  .header__slider-item-text-top {
    font-size: 24px;
  }

  .header__slider-item-text-about,
  .header__slider-item-text-top-about {
    font-size: 13px;
  }

  .header__slider-item-text {
    padding: 15px;
  }
}

@media (max-width: 550px) {

  .header__slider-item-text {
    left: 0;
  }
}


@media (max-width: 1100px) {

  .header__fixed-wrap {
    justify-content: space-around;
  }

  .header__fixed-menu {
    display: none;
  }
}

@media (max-width: 1280px) {

  .header__fixed-contacts-email {
    display: none;
  }
}

@media (max-width: 590px) {

  .header__fixed-contacts {
    display: none;
  }
}

@media (max-width: 768px) {

  .header__top {
    height: 230px;
  }

  .header__top-container {
    flex-flow: column nowrap;
    padding: 0;
  }

  .header__top-inner {
    background-size: 400%;
    background-position-x: center;
  }

  .header__top-logo {
    margin-bottom: 15px;
  }

  .header__top-logo-brand {
    font-size: 35px;
  }

  .header__top-logo-info {
    font-size: 23px;
  }
}

@media (max-width: 480px) {

  .header__fixed-auth {
    display: block;
    border: 0;
    width: 25px;
    height: 25px;
    background: url(../img/enter.svg) no-repeat;
    background-size: cover;
    position: relative;
  }

  .header__fixed-dropdown {
    flex-flow: column nowrap;
    position: absolute;
    top: 40px;
    align-items: flex-start;
    width: 50%;
    max-width: 200px;
    height: 70px;
    background-color: #dedcca;
    padding-left: 30px;
    left: 13px;
    display: none;
  }

  .drop-auth.header__fixed-dropdown {
    display: block;
  }

  .header__fixed-dropdown a {
    display: inline-block;
    padding: 10px;
  }

  .header__fixed-login,
  .header__fixed-registration {
    line-height: 12px;
  }

  .header__fixed-login:hover,
  .header__fixed-registration:hover {
    padding: 10px;
    line-height: 12px;
  }

  .header__top-container {
    padding-bottom: 20px;
  }

  .header__top-contacts-tel {
    font-size: 16px;
  }

  .header__top-contacts-info {
    font-size: 11px;
  }

  .header__top-logo-brand {
    font-size: 22px !important;
  }

  .header__top-logo-info {
    font-size: 15px !important;
  }
}

@media (max-width: 1000px) {

  .header__slider {
    display: none;
  }

  .index-page .header__top {
    display: block;
  }
}

@media (min-width: 891px) and (max-width: 1049px) {

  .navigation__logo-pic img {
    width: 100px;
    height: 150px;
  }
}
