// В этом файле должны быть стили для БЭМ-блока news-list, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.news-list {
  max-width: 1000px;
  width: 100%;
  padding-left: 35px;
  margin-top: 50px;
}

.news-list__title {
  text-transform: uppercase;
  line-height: 3;
  font-family: Philosopher, sans-serif;
  border-bottom: 1px dotted #e0caca;
}

.news-list__block {
  padding: 15px 0;
  border-bottom: 1px dotted #e0caca;
}

.news-list__block-title {
  text-transform: uppercase;
  font-size: 14px;
}

.news-list__block-title-date {
  color: #808080;
  font-size: 14px;
}

.news-list__block-content {
  margin-top: 15px;
}

.news-list__block-content-text {
  font-size: 12px;
  color: #333;
  width: 87%;
}

.news-list__block-content-text-link {
  font-weight: 600;
}

.news-list__block-content-img {
  display: flex;
  justify-content: flex-end;
  width: 13%;
}

.news-list__block-content-img-item {
  max-width: 100px;
  width: 100%;
  height: auto;
}

@media (min-width: 769px) {

  .news-list__block-content {
    display: flex;
    justify-content: flex-between;
  }
}

@media (max-width: 768px) {

  .news-list__block-content-text {
    width: 100%;
  }

  .news-list__block-content-img {
    width: 100%;
  }
}

@media (max-width: 860px) {

  .news-list {
    padding: 0;
  }
}

